.container{
    height: 575px;
    width: 85%; 
    position: absolute;
    transform: translate(-50% , -50%);
    top: 50%;
    left: 50%;
    background: url(https://images.pexels.com/photos/36487/above-adventure-aerial-air.jpg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
   
    
}


.info{
    background-color: #212e3e73;
    height: 100%;
    width: 40%;
    position: absolute;
    top: 0;
    right: 0;
}

.brand{
    color: white;
    margin:  20px;font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 15px;
}


.searchBox{
    background-color:#1e445bb2;
    width: 70%;
    margin: 15px auto;
    position: relative;

   
    
}

.searchBox input{
    background: transparent;
    border: none;
    outline: none;
    padding: 10px;
    color: white;
    width: 80%;
    font-size: 20px;
}
.searchBox button{
    position: absolute;
    height: 100%;
    width: 20%;
    top: 0;
    right: 0;
    cursor: pointer;
    background-color: #562234;
    color: white;
    border: none;
}


.weatherInfo{
    color: white;
    font-weight: 400;
    border-bottom: 0.5px solid #ccc;
    padding: 10px 15px;
    width: 85%;
    margin: auto;
}

.infoItems li{
    color: #ccc;
    margin: 20px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.infoItems li span{
    margin-right: 40px;
}

.degree{
    position: absolute;
    left: 65px;
    bottom: 60px;
    width: fit-content;
    height: auto;
    display: flex;
    align-items: center;
}

.degree h1{
    font-size: 80px;
    color: white;
    font-weight: 600;
}

.name{
    margin-left: 25px;
    color: white;
}
.name h2{
    margin: 5px 0px;
    font-size: 35px;
    font-weight: 600;
}

.name span{
    font-size: 12px;
}

.name p{
    margin: 0;
}


.weather_logo{
    margin-left: 10px;
    color: white;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.weather_logo p{
    margin: 0px;
}

.weather_logo img{
    width: 75px;
}

.d_flex{
    display: flex;
    align-items: center;
}

.loadder{
    background-color: rgba(0, 0, 0, 0.26);
    position: fixed;
    width: 100%;
    height: 100%;
}

.loadder img{
    width: 10%;
    position: absolute;
    transform: translate(-50% , -50%);
    top: 50%;
    left: 50%;
}

.errMsg{
    position: absolute;
    bottom: -45px;
    right: 0;
    background-color: #212e3e00;
    color:red;
    width: 50%;
    margin: 0px auto;
    padding: 8px;
    animation: error 0.2s 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

@keyframes error {
    0%{
        transform: translateY(15px);
    }
    50%{
        transform: translateY(-15px);
    }
    100%{
        transform: translateY(0);
    }
}




@media screen and (max-width: 1200px) {
    .degree{
        position: unset;
        margin-top: 50px;
        padding: 0px 30px;
    }

    .info{
        position: unset;
        height: auto;
        width: 100%;
        padding: 10px 0px;
    }

    .searchBox{
        margin-top: 10px;
    }
    
    
    .container{
        height: auto;
        position: unset;
        margin: auto;
        transform: none;

    } 

     .degree h1 {
        font-size: 72px;
        color: white;
        font-weight: 600;
    }

    .name h2 {
        margin: 5px 0px;
        font-size: 27px;
        font-weight: 600;
    }

    .name p {
        margin: 0;
        font-size: 12px;
    } 


    .brand{
        padding-top: 10px;
    }
} 


@media screen and (max-width: 720px){

     .degree h1 {
        font-size: 41px;
        color: white;
        font-weight: 600;
    }

    .name h2 {
        margin: 5px 0px;
        font-size: 27px;
        font-weight: 600;
    }

    .name p {
        margin: 0;
        font-size: 12px;
    } 

    .degree{
        flex-direction: column;
    }

    .brand{
        padding-top: 10px;
    }

    .searchBox input {
        font-size: 10px;
    }

    .infoItems li{
        font-size: 12px;
    }

    .errMsg{
        font-size: 12px;
    }

    
}

@media screen and (max-width: 375px){
    
    .weather_logo img{
        width: 50px;
    }
    
    .brand{
        padding-top: 10px;
    }
    .container {
        width: 100%;
        height: 100%;
    }
}

